#page-landing {
    
    
    
    width: 100vw; 
    height: 100vh;

    
    
    
    display: flex; 
    justify-content: center; 
    align-items: center; 

    
    
    
    color: var(--color-text-in-primary);
    background-color: var(--color-primary);

}



.logo-container{
    text-align: center; 
    margin-bottom: 3.2rem;
}

.logo-container img{
    height: 10rem; 
}

.logo-container h2{
    font-weight: 500; 
    font-size: 2.4rem; 
    line-height: 4.6rem; 
    margin-top: 0.8rem; 
}

.hero-image{   
    width: 100%; 
}


.buttons-container{
    display: flex; 
    justify-content: center;
    margin: 3.2rem 0; 
}

.buttons-container a {
    width: 30rem;
    height: 10.4rem;
    border-radius: 0.8rem;
    
    font: 700 2.0rem Archivo; 

    display: flex;
    align-items: center;
    justify-content: center;

    text-decoration: none; 
    color:var(--color-button-text);

   transition:background-color 0.3s
}
.buttons-container a:first-child{
    margin-right: 1.6rem; 
}
.buttons-container img{
    width: 4rem;   
}

.buttons-container a.study{
    background:var(--color-primary-lighter);
}

.buttons-container a.give-classes{
    background: var(--color-secundary);
}

.buttons-container a.study:hover{
    background:var(--color-primary-light);
} 

.buttons-container a.give-classes:hover{
    background: var(--color-secundary-dark);
}

.total-connections{
    font-size: 1.4rem;

    display: flex;
    align-items: center;
    justify-content: center;

}

.total-connections img{
    margin-left: 0.8rem;

}






    
@media(min-width:1100px){

    #page-landing-content{
        max-width: 1100px;

        display: grid; 
        grid-template-rows:350px 1fr ;  
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-areas: 
        "logo hero hero"
        "buttons buttons total";
    }
    .logo-container{
    
       grid-area: logo; 
       align-self: center ; 
       margin:0;
       text-align: left;
    }
    .logo-container h2{
      text-align: left;
      text-align: initial; 
      font-size: 3.6rem;
     }

     .logo-container img{
        height: 100%;
       }


    .hero-image{
        grid-area: hero; 
        align-self: end; 
    }

    .buttons-container{
        grid-area: buttons; 
        justify-self: flex-start; 
    }

    .buttons-container{
       font-size: 2.4rem;
    }

    .buttons-container a img{
        margin-right:2.4rem;
     }

    .total-connections{
        grid-area: total; 
        justify-self: end; 
    }

}



.input-block{
   position: relative;
}
.input-block + .input-block {
    margin-top:1.4rem ;
 }
 

.input-block label{ 
    font-size: 1.4rem;
    
 }

.input-block input{ 
    width: 100%;
    height: 5.6rem;
    margin-top: 0.8rem;
    border-radius: 0.8rem;
    background: var(--color-input-background);
    border: 1px solid var(--color-line-in-white);
    outline:0;
    padding: 0 1.6rem;
    font:1.6rem Archivo; 
 }
 
 
   .input-block:focus-within::after{
     width: calc(100% - 3.2rem);
     height: 2px;
     content: '';
     background: var(--color-primary-light);
     position:absolute ;
     left: 1.6rem;
     right:1.6rem;
     bottom: 0;
  }
.page-header{
    display: flex;
    flex-direction: column;
    background-color: var(--color-primary);
  
}



.page-header .top-bar-container{
    width: 90%;
    margin: 0 auto; /* -top bottom- -right left-  -*/
    display: flex;
    justify-content: space-between; /*Espaçamentos entre dois elemnto - left <-> right*/
    align-items: center;
    color: var( --color-text-in-primary);
    padding: 1.6rem 0;

}

.page-header .top-bar-container > a {
   height: 3.2rem;
   transition: opacity 0.2s;
}

.page-header .top-bar-container > a:hover { 
    opacity: 0.6; 
 }



.page-header .top-bar-container > img {
    height: 1.6rem;
}

.page-header .header-content{
    width: 90%;
    position: relative;
    margin: 3.2rem auto;
}
.page-header .header-content strong{
    font: 700 3.6rem Archivo;
    line-height: 4.2rem;
    color:var(--color-title-in-primary)
}

.page-header .header-content p {
    max-width: 30rem;
    font-size: 1.6rem;
    line-height: 2.6rem; /*Espaço entre linhas de texto*/
    color: var(--color-text-in-primary);
    margin-top: 2.4rem;
}


/***********************************/
    /*TELA  >= 700PX* DESKTOP /
/***********************************/

@media(min-width:700px){
    .page-header{
        height: 340px;
        
    }

    .page-header .top-bar-container{
        max-width: 1100px;
    }

    .page-header .header-content{
        flex:1 1;  /*Prenche de forma vertical o espaço de sobra da div Pai*/
        max-width: 740px;
        margin: 0 auto;
        padding-bottom: 48px;
        display: flex;
        flex-direction: column; /*Direção de fluxo será em coluna*/
        justify-content: center; /*Esta margem se torna vertical com flex-direction:column */
        align-items: flex-start; /*Posiciona itens a esquerda*/
    }

    .page-header .header-content strong{
        max-width: 350px;
    }

}


#page-login{
    width: 100vw;
    height: 100vh;
}

#page-login main{

    text-align: center;

   ;
     
     
 }

@media(min-width:700px){
     #page-login main{

        text-align: center;
        margin: 0 auto;
         padding: 3.2rem 0;
         max-width: 740px;
         
         
     }
     input{
         width: 100%;
         font-size:larger;

     }

     button{
         font-size: large;
         font-weight: bold;
         margin-top: 30px;
         width: 30%;
         border-radius: 15px;
     }
     button:hover{
        margin-top: 30px;
        width: 30%;
        background-color: #8257e5;
        color :#fafafa; 
    }

}



.select-block{
   position: relative;
}
.select-block + .select-block {
    margin-top:1.4rem ;
 }
 

.select-block label{ 
    font-size: 1.4rem;
    
 }

.select-block select{ 
    width: 100%;
    height: 5.6rem;
    margin-top: 0.8rem;
    border-radius: 0.8rem;
    background: var(--color-input-background);
    border: 1px solid var(--color-line-in-white);
    outline:0;
    padding: 0 1.6rem;
    font:1.6rem Archivo; 
 }
 
 
   .select-block:focus-within::after{
     width: calc(100% - 3.2rem);
     height: 2px;
     content: '';
     background: var(--color-primary-light);
     position:absolute ;
     left: 1.6rem;
     right:1.6rem;
     bottom: 0;
  }
#page-LogIn-form{
    
    width: 100vw;
    height: 100vh;
}

#page-LogIn-form .page-header .header-content{
    margin-bottom: 6.4rem;
}


#page-LogIn-form main{
    background: var(--color-box-base);
    width: 100%;
    max-width: 74rem;
    border: 0.8rem;
    margin:-3.2rem auto 3.2rem;
    overflow: hidden;
    border-radius: 2rem;
    padding-top: 2rem;
}
#page-LogIn-form main label{
 color:var(--color-text-complement)   
}


#page-LogIn-form main fieldset{
    padding: 0 2.4rem;
    border:0;
}

#page-LogIn-form main fieldset + fieldset{
    margin-top: 6.4rem;
    
}

#page-LogIn-form main fieldset legend{
    font: 700 2.4rem Archivo;
    color: var(--color-text-title);
    margin-bottom: 2.4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-bottom: 1.6rem;
    border-bottom: 1px solid var(--color-line-in-white);
}

#page-LogIn-form main fieldset legend button{
    background: none;
    border: 0;
    cursor: pointer;
    color: var(--color-primary);
    font: 700 1.8rem Archivo;
    transition: color 0.2s ;
}

#page-LogIn-form main fieldset legend button:hover{
    color:var(--color-primary-dark)
}


#page-LogIn-form main fieldset .select-block + .input-block ,  
#page-LogIn-form main fieldset .input-block + .textarea-block {
  margin-top: 2.4rem;
}


#page-LogIn-form main footer{
    border: 1px solid var(--color-line-in-white);
    padding: 2rem;
    margin: 2.4rem;
    background: var(--color-box-footer);
}

#page-LogIn-form main footer p{
    display: flex;
    justify-content: center;
    color: var(--color-text-complement);
    font-size: 1.4rem;

}
#page-LogIn-form main footer p img{
    margin-right: 1.5rem;

}

#page-LogIn-form main footer button{
    width: 100%;
    border-radius: 0.8rem;
    height: 5rem;
    margin-top: 2rem;
    color: var(--color-button-text);
    background: var( --color-secundary);
    border: 0;
    cursor: pointer;
    font: 700 1.6rem Archivo;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    transition: 0.5s;
}

#page-LogIn-form main footer button:hover{
    background: var(--color-secundary-dark)
}


    
@media(min-width:700px){
    #page-LogIn-form .page-header .header-content{
        margin-bottom: 0;
    } 
    #page-LogIn-form main fieldset{
       padding: 0 6.4rem;
    }

    #page-LogIn-form main footer{
        padding: 4.0rem 6.4rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    #page-LogIn-form main footer button{
        width: 20rem;
        margin-top: 0;
    }
    #page-LogIn-form main .schedule-item {
        display: grid;
        grid-template-columns: 2fr 1fr 1fr ;  
        grid-column-gap: 1.6rem;  
        -webkit-column-gap: 1.6rem;  
                column-gap: 1.6rem;  
    }
    #page-LogIn-form main .schedule-item .input-block{
        margin-top: 0 !important; 
    }
 
     
}

